* {
  box-sizing: border-box;
}

body {
  background: $c-gray;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 27px;
}

h2 {
  font-size: 48px;
  line-height: 48px;
  font-weight: bold;
  letter-spacing: -1.5px;

  @media (max-width: 768px) {
    font-size: 36px;
    line-height: 36px;
  }
}

h3 {
  font-size: 24px;
  line-height: 30px;
  font-weight: bold;
  color: $primary-color;
  padding-bottom: 5px;
}

section {
  padding: 76px 0;

  @media (max-width: 1200px) {
    padding: 50px 0;
  }

  @media (max-width: 768px) {
    padding: 30px 0;
  }
}

p {
  margin: 15px 0 30px;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;
}

button {
  border-radius: $primary-border-radius;
  border: none;
  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
  cursor: pointer;
}

input,
textarea {

  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: #000;

  &::placeholder {
    font-family: Roboto, sans-serif;
    font-size: 14px;
    line-height: 14px;
    color: #b9c1ce;
  }
}

input,
button,
textarea {

  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px $primary-color;
    border-radius: 4px;
  }
}

.bold {
  font-weight: bold;
}

.container {
  width: 1000px;
  margin: 0 auto;

  @media (max-width: 1200px) {
    width: 90%;
  }
}

.section-title {
  width: 100%;
  text-align: center;
  position: relative;
  margin-bottom: 90px;

  @media (max-width: 576px) {
    margin-bottom: 50px;
  }

  &::after {
    content: "";
    width: 23px;
    height: 16px;
    background: url(../images/crown.svg) no-repeat center;
    position: absolute;
    bottom: -24px;
    left: 50%;
    transform: translateX(-50%);
  }
}
